*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url("https://www.exoticca.com/es/blog/wp-content/uploads/2019/02/Estas-son-las-mejores-playas-de-Brasil-Te-las-vas-a-perder.jpg");
  background-image: no-repeat;
  background-image: fixed;
  background-size: cover;
  background-image: no-repeat;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.input-icon{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  
}

/* Input */

.input{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 3rem;
  padding-left: 50px;
  border-radius: 20px;
  background-color: rgb(0, 0, 0, 0.6);
}

.input-value{
  outline: none;
  border: none;
  color: beige;
  background-color: transparent;
  font-size: 1rem;
  width: 20rem;
}

.input-icon{
  width: 3rem;
  height: 100%;
  margin-right: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-left: 1px solid #adacac;
  cursor: pointer;
}
.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: azure;

}

.weather {
  width: 35rem;
  height: 28rem;
  background-color: rgb(0, 0, 0, 0.6);
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  padding: 2rem;
  justify-content: space-around;
}

.weather-location{
  font-size: 2rem;
  font-weight: 500;

}

.weather-degree{
  font-size: 2.5rem;
  font-weight: 400;
}

.weather-description{
  font-size: 20px;
}

.weather-icon{
  font-size: 45px;
  margin-right: 0.4rem;
}

.weather-description{ 
  display: flex;
  align-items: center;

}

.weather-description-head{
  display: flex;
  align-items: center;
}

.weather-description  h3{
  font-size: 1.2rem;
  font-weight: normal;
  margin-right: 1rem;
  margin-bottom: 0.7rem;
  padding: 10px;
}

.weather-date{
  font-size: 1rem;
  font-weight: normal;
  color: gray;
}

.weather-country{
  padding-bottom: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: end;
}

.weather-display{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-top: 1rem;
  height: 17rem;

}



